<template>
    <v-container fluid fill-height style="padding:0;" id="basis" grid-list-md>
        <Blog :items="items" :time="time" :title="title">
            <div class="blogcontent">
                <v-layout row wrap>
                    <v-flex xs12 v-for="(item, index) in parts" :key="index">
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="200"
                            >
                                <v-card-title>
                                    <h3 :id="item.id">{{item.title}}</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <v-layout row wrap>
                                        <v-flex xs12 sm6 pr-1>
                                            <div class="part" v-html="item.html"></div>
                                        </v-flex>
                                        <v-flex xs12 sm6>
                                            <v-tabs color="light-green darken-1" dark slider-color="yellow">
                                                <v-tab key="explain" ripple>解释</v-tab>
                                                <v-tab key="html" ripple>HTML</v-tab>
                                                <v-tab key="css" ripple  v-if="item.css">CSS</v-tab>
                                                <v-tab-item key="explain">
                                                    <v-card flat>
                                                        <v-card-text v-html="item.explain"></v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="html">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">{{item.html}}</pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="css" v-if="item.css">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">{{item.css}}</pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                            </v-tabs>
                                        </v-flex>
                                    </v-layout>
                                    
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                </v-layout>
            </div>
        </Blog>
    </v-container>
</template>

<script>
import Blog from "../Blog.vue";
export default {
    components: { Blog },
    data() {
        return {
            time: "2018-10-25",
            title: "svg基础应用",
            items: [
                {
                    title: "绘制矩形",
                    target: "#rect"
                },
                { divider: true },
                {
                    title: "绘制圆形",
                    target: "#circle"
                },
                { divider: true },
                {
                    title: "绘制椭圆",
                    target: "#ellipse"
                },
                { divider: true },
                {
                    title: "绘制直线",
                    target: "#line"
                },
                { divider: true },
                {
                    title: "绘制折线",
                    target: "#polyline"
                },
                { divider: true },
                {
                    title: "绘制闭合折线",
                    target: "#polygon"
                },
                { divider: true },
                {
                    title: "绘制路径",
                    target: "#path"
                },
                { divider: true },
                {
                    title: "线性渐变",
                    target: "#linearGradient"
                },
                { divider: true },
                {
                    title: "径向渐变",
                    target: "#radialGradient"
                },
                { divider: true },
                {
                    title: "模式",
                    target: "#pattern"
                },
                { divider: true },
                {
                    title: "绘制文本",
                    target: "#text"
                },
                { divider: true },
                {
                    title: "过渡",
                    target: "#transform"
                },
                { divider: true },
                {
                    title: "裁剪",
                    target: "#clipPath"
                },
                { divider: true },
                {
                    title: "蒙层",
                    target: "#mask"
                },
                { divider: true },
                {
                    title: "利用animate移动图形",
                    target: "#animate"
                },
                { divider: true },
                {
                    title: "利用animate绘制五角星",
                    target: "#use-animate"
                },
                { divider: true },
                {
                    title: "让一个图形沿着指定路径移动",
                    target: "#animateMotion"
                },
                { divider: true },
                {
                    title: "过渡动画",
                    target: "#animateTransform"
                }
            ],
            parts: [
{
    id: 'rect',
    title: '绘制矩形',
    explain: `
    <table>
        <tr>
            <td> </td>
            <th>属性名</th>
            <th>示例</th>
            <th>说明</th>
        </tr>
        <tr>
            <th>1</th>
            <td>x</td>
            <td>0</td>
            <td>矩形的x轴坐标</td>
        </tr>
        <tr>
            <th>2</th>
            <td>y</td>
            <td>0</td>
            <td>矩形的y轴坐标</td>
        </tr>
        <tr>
            <th>3</th>
            <td>width</td>
            <td>100</td>
            <td>矩形的宽度</td>
        </tr>
        <tr>
            <th>4</th>
            <td>height</td>
            <td>100</td>
            <td>矩形的高度</td>
        </tr>
        <tr>
            <th>5</th>
            <td>rx</td>
            <td>5</td>
            <td>水平方向的圆角</td>
        </tr>
        <tr>
            <th>6</th>
            <td>ry</td>
            <td>5</td>
            <td>垂直方向的圆角</td>
        </tr>
    </table>
    `,
    html: `
    <svg width="200" height="200" xmlns="http://www.w3.org/2000/svg">
        <rect width="70" height="50" fill="green"/>
        <rect x="0" y="60" width="70" height="50" rx="15" ry="30" fill="none" stroke="darkseagreen"/>
    </svg>
    `,
}, {
    id: 'circle',
    title: '绘制圆形',
    explain: `
    <table>
        <tr>
            <td> </td>
            <th>属性名</th>
            <th>示例</th>
            <th>说明</th>
        </tr>
        <tr>
            <th>1</th>
            <td>cx</td>
            <td>0</td>
            <td>圆心x坐标</td>
        </tr>
        <tr>
            <th>2</th>
            <td>cy</td>
            <td>0</td>
            <td>圆心y坐标</td>
        </tr>
        <tr>
            <th>3</th>
            <td>r</td>
            <td>10</td>
            <td>圆半径</td>
        </tr>
    </table>
    `,
    html: `
    <svg width="200" height="200" xmlns="http://www.w3.org/2000/svg">
        <circle cx="30" cy="30" r="30" fill="green"/>
    </svg>
    `,
}, {
    id: 'ellipse',
    title: '绘制椭圆',
    explain: `
    <table>
        <tr>
            <td> </td>
            <th>属性名</th>
            <th>示例</th>
            <th>说明</th>
        </tr>
        <tr>
            <th>1</th>
            <td>cx</td>
            <td>0</td>
            <td>中心x坐标</td>
        </tr>
        <tr>
            <th>2</th>
            <td>cy</td>
            <td>0</td>
            <td>中心y坐标</td>
        </tr>
        <tr>
            <th>3</th>
            <td>rx</td>
            <td>10</td>
            <td>水平方向半径</td>
        </tr>
        <tr>
            <th>4</th>
            <td>ry</td>
            <td>10</td>
            <td>垂直方向半径</td>
        </tr>
    </table>
    `,
    html: `
    <svg width="200" height="200" xmlns="http://www.w3.org/2000/svg">
        <ellipse cx="30" cy="70" rx="30" ry="70" fill="none" stroke="darkseagreen"/>
    </svg>
    `,
}, {
    id: 'line',
    title: '绘制直线',
    explain: `
    <p class=".body-1">1. <code>(x1, y1) (x2, y2)</code>表示直线的两点</p>
    <p class=".body-1">2. <code>stroke-dasharray</code>属性可以设置虚线</p>
    `,
    html: `
    <svg width="200" height="200" xmlns="http://www.w3.org/2000/svg">
        <line x1="0" y1="0" x2="100" y2="100" stroke="green" />
        <line x1="0" y1="50" x2="100" y2="50" stroke="green" stroke-dasharray="5,5" />
    </svg>
    `,
}, {
    id: 'polyline',
    title: '绘制折线',
    explain: `
    <p class=".body-1">1. <code>points</code>属性表示一串由空格分隔的点</p>
    <p class=".body-1">2. <code>stroke-linecap</code>属性可以设置线端点样式</p>
    <p class=".body-1">3. <code>stroke-linejoin</code>属性可以设置线相交点样式</p>
    `,
    html: `
    <svg width="200" height="200" xmlns="http://www.w3.org/2000/svg">
        <polyline 
            points="0,20 5,25 10,20 15,25 20,20 25,25 30,20"
            fill="none" 
            stroke="darkseagreen"
            stroke-width="4" 
            stroke-linecap="round"
            stroke-linejoin="round"
        />
    </svg>
    `,
}, {
    id: 'polygon',
    title: '绘制闭合折线',
    explain: `
    <p class=".body-1">和<code>polyline</code>用法一致，只不过<code>polygon</code>会将折线的开始和结束点相连</p>
    `,
    html: `
    <svg width="200" height="200" xmlns="http://www.w3.org/2000/svg">
        <polygon points="50,50 20,100 90,70 10,70 80,100" fill="green"/>
    </svg>
    `,
}, {
    id: 'path',
    title: '绘制路径',
    explain: `
    <p class=".body-1">下面列出<code>path</code>中能用到的所有事件名(大写表示绝对位置，小写表示相对位置)</p>
    <table>
        <tr>
            <td> </td>
            <th>事件名</th>
            <th>示例</th>
            <th>说明</th>
        </tr>
        <tr>
            <th>1</th>
            <td>M</td>
            <td>M10 10</td>
            <td>MoveTo将画笔移动到指定点</td>
        </tr>
        <tr>
            <th>2</th>
            <td>L</td>
            <td>L10 10</td>
            <td>LineTo将画笔画到指定点</td>
        </tr>
        <tr>
            <th>3</th>
            <td>H</td>
            <td>H 10</td>
            <td>画一条水平线，只需给出x轴坐标</td>
        </tr>
        <tr>
            <th>4</th>
            <td>V</td>
            <td>V 90</td>
            <td>画一条垂直线，只需给出y轴坐标</td>
        </tr>
        <tr>
            <th>5</th>
            <td>Z</td>
            <td>Z</td>
            <td>Z命令表示将当前点和第一个点连接</td>
        </tr>
        <tr>
            <th>6</th>
            <td>C</td>
            <td>C 30,30 70,170 90,100</td>
            <td>三次贝塞尔曲线，前两个点表示控制点，最后一个点表示结束点</td>
        </tr>
        <tr>
            <th>7</th>
            <td>S</td>
            <td>S 150,170 170,100</td>
            <td>三次贝塞尔曲线，一般跟在C指令后面用来延续贝塞尔曲线，<br>它的第一个控制点是前面指令第二个控制点的中心对称点，<br>参数表示的一个点是第二个控制点</td>
        </tr>
        <tr>
            <th>8</th>
            <td>Q</td>
            <td>Q 30,130 50 200</td>
            <td>二次贝塞尔曲线，只有一个控制点</td>
        </tr>
        <tr>
            <th>9</th>
            <td>T</td>
            <td>T 90 200</td>
            <td>二次贝塞尔曲线，作用和S的原理类似</td>
        </tr>
        <tr>
            <th>10</th>
            <td>A</td>
            <td>A p1 p2 p3 p4 p5 p6 p7</td>
            <td>
                绘制圆弧<br>
                p1: 水平半径<br>
                p2: 垂直半径<br>
                p3: 旋转角度<br>
                p4: 弧线是否大于180度<br>
                p5: 1表示顺时针，0表示逆时针<br>
                p6: 结束点横坐标<br>
                p7: 结束点纵坐标<br>
                如果结束点不在圆弧内，圆弧将按照尽可能小的扩张到此点
            </td>
        </tr>
        
    </table>
    `,
    html: `
    <svg width="400" height="300" xmlns="http://www.w3.org/2000/svg">
        <path 
            d="M10 100 C 30,30 70,170 90,100 S 150,170 170,100" 
            fill="none" stroke="black"/>
        <path 
            d="M10 200 Q 30,130 50 200 T 90 200" 
            fill="none" stroke="black"/>
        <path 
            d="M200 100 A 30 50 0 0 0 230 150" 
            fill="none" stroke="red"/>
        <path 
            d="M200 100 A 30 50 0 1 0 230 150" 
            fill="none" stroke="green"/>
        <path 
            d="M200 100 A 30 50 0 0 1 230 150" 
            fill="none" stroke="blue"/>
        <path 
            d="M200 100 A 30 50 0 1 1 230 150" 
            fill="none" stroke="pink"/>
    </svg>
    `,
}, {
    id: 'linearGradient',
    title: '线性渐变',
    explain: `
    <p class=".body-1">1. 线性渐变默认是水平排列</p>
    <p class=".body-1">2. 指定<code>x1="0" x2="0" y1="0" y2="1"</code>可以使渐变垂直排列</p>
    <p class=".body-1">3. 使用<code>xlink</code>可以引用其他渐变色</p>
    `,
    html: `
    <svg width="200" height="220" xmlns="http://www.w3.org/2000/svg">
        <linearGradient id="H-lg">
            <stop stop-color="skyblue" stop-opacity="1" offset="0%"/>
            <stop stop-color="white" stop-opacity="0.5" offset="50%"/>
            <stop stop-color="green" stop-opacity="1" offset="100%"/>
        </linearGradient>
        <linearGradient 
            id="V-lg" x1="0" x2="0" y1="0" y2="1"
            xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#H-lg"/>

        <rect x="10" y="10" width="100" height="100" fill="url(#H-lg)"/>
        <rect x="10" y="120" width="100" height="100" fill="url(#V-lg)"/>
    </svg>
    `,
}, {
    id: 'radialGradient',
    title: '径向渐变',
    explain: `
    <p class=".body-1">1. <code>(cx, cy)</code>设置渐变中心的位置，值是占比</p>
    <p class=".body-1">2. <code>r</code>设置渐变的半径，值是占比</p>
    <p class=".body-1">3. <code>(fx, fy)</code>设置渐变色中心的起始位置点，值是占比</p>
    <p class=".body-1">4. 如果你不想用占比设置上面的属性，设置<code>gradientUnits="userSpaceOnUse"</code>即可</p>
    `,
    html: `
    <svg width="200" height="220" xmlns="http://www.w3.org/2000/svg">
        <linearGradient id="lg" x1="0" x2="0" y1="0" y2="1">
            <stop stop-color="skyblue" stop-opacity="1" offset="0%"/>
            <stop stop-color="skyblue" stop-opacity="1" offset="50%"/>
            <stop stop-color="#f6f7f7" stop-opacity="1" offset="75%"/>
            <stop stop-color="white" stop-opacity="0.3" offset="80%"/>
            <stop stop-color="green" stop-opacity="1" offset="100%"/>
        </linearGradient>
        <radialGradient id="rg" cx="0.5" cy="0.5" r="0.5" fx="0.25" fy="0.25">
            <stop offset="0%" stop-color="#fff"/>
            <stop offset="100%" stop-color="skyblue"/>
        </radialGradient>

        <rect x="10" y="10" width="100" height="100" fill="url(#lg)"/>
        <circle cx="30" cy="30" r="10" fill="url(#rg)"/>
    </svg>
    `,
}, {
    id: 'pattern',
    title: '模式',
    explain: `
    <p class=".body-1">1. <code>pattern</code>默认的单位系统是相对的而它内部的元素是绝对的，这在应用上面会出现问题</p>
    <p class=".body-1">2. <code>patternContentUnits="objectBoundingBox"</code>可以将模式的单位统一成相对的</p>
    <p class=".body-1">3. <code>patternUnits="userSpaceOnUse"</code>可以将模式的单位统一成绝对的</p>
    `,
    html: `
    <svg width="200" height="200" xmlns="http://www.w3.org/2000/svg">
        <pattern 
            id="pt-relative" x="0" y="0" width=".1" height=".1" 
            patternContentUnits="objectBoundingBox">
            <circle cx=".05" cy=".05" r=".05" fill="green"/>
        </pattern>
        <rect x="0" y="0" width="100" height="100" fill="url(#pt-relative)"/>

        <pattern 
            id="pt-absolute" x="0" y="0" width="10" height="10" 
            patternUnits="userSpaceOnUse">
            <circle cx="5" cy="5" r="5" fill="green"/>
        </pattern>
        <rect x="100" y="100" width="100" height="100" fill="url(#pt-absolute)"/>
    </svg>
    `,
}, {
    id: 'text',
    title: '绘制文本',
    explain: `
    <p class=".body-1">1. <code>text</code>标签可以直接设置html字体的属性</p>
    <p class=".body-1">2. <code>text-anchor</code>设置字体的书写方向</p>
    <p class=".body-1">3. <code>tspan</code>标签用作大块儿文本的一部分只能作为<code>text</code>或者<code>tspan</code>的子元素</p>
    <table>
        <tr>
            <td> </td>
            <th>属性名</th>
            <th>示例</th>
            <th>说明</th>
        </tr>
        <tr>
            <th>1</th>
            <td>x or y</td>
            <td>10 20 30</td>
            <td>每一个字符的绝对位置，可以是一连串的数字</td>
        </tr>
        <tr>
            <th>2</th>
            <td>dx or dy</td>
            <td>1 2 3</td>
            <td>每一个字符的相对位置，可以是一连串的数字</td>
        </tr>
        <tr>
            <th>3</th>
            <td>rotate</td>
            <td>45</td>
            <td>每一个字符的旋转角度，可以是一连串的数字</td>
        </tr>
    </table>
    <p class=".body-1">4. <code>textPath</code>可以将文字沿着指定的<code>path</code>排列</p>
    `,
    html: `
    <svg width="200" height="200" xmlns="http://www.w3.org/2000/svg">
        <text x="50" y="40" text-anchor="start" class="svgfont" fill="aqua">
            SVG<tspan fill="pink" dx="1 2 3 4 5 6 7" rotate="45">awesome</tspan>
        </text>
        
        <path 
            d="M10 200 Q 30,130 50 200" 
            fill="none" id="tp"/>
        <text>
            <textPath 
                xmlns:xlink="http://www.w3.org/1999/xlink" 
                xlink:href="#tp">
                AWESOME SVG
            </textPath>
        </text>
    </svg>
    `,
    css: `
    .svgfont{
        font-family: cursive;
        font-size: 20px;
        font-weight: 400;
    }
    `
}, {
    id: 'transform',
    title: '过渡',
    explain: `
    <p class=".body-1">1. <code>transform</code>的使用会创建新的坐标体系，要注意使用过渡后此坐标非彼坐标。过渡的属性和CSS3的一样</p>
    <p class=".body-1">2. <code>g</code>能够将过渡应用到一组元素上</p>
    `,
    html: `
    <svg width="200" height="200" xmlns="http://www.w3.org/2000/svg">
        <rect x="0" y="0" width="30" height="30" 
            transform="translate(10,10)" fill="green" />
        <g transform="rotate(45)" fill="green">
            <rect x="80" y="0" width="10" height="10"  />
            <rect x="90" y="10" width="10" height="10"  />
            <rect x="100" y="20" width="10" height="10"  />
        </g>
        <rect x="50" y="10" width="30" height="30" 
            transform="skewX(30)" fill="green" />
        <rect x="240" y="10" width="30" height="30" 
            transform="scale(0.5, 1)" fill="green" />
    </svg>
    `,
}, {
    id: 'clipPath',
    title: '裁剪',
    explain: `
    <p class=".body-1"><code>clipPath</code>中定义的图形和使用此属性的图形的交集才会显示出来</p>
    `,
    html: `
    <svg width="200" height="200" xmlns="http://www.w3.org/2000/svg">
        <clipPath id="cp">
            <rect x="0" y="0" width="50" height="100" />
        </clipPath>

        <circle cx="100" cy="100" r="100" clip-path="url(#cp)" fill="green" />
    </svg>
    `,
}, {
    id: 'mask',
    title: '蒙层',
    explain: `
    <p class=".body-1"><code>mask</code>的作用是根据颜色明度作为应用<code>mask</code>元素的透明度来实现蒙层效果的</p>
    `,
    html: `
    <svg width="200" height="200" xmlns="http://www.w3.org/2000/svg">
        <linearGradient id="mask-lg-l">
            <stop offset="0" stop-color="black" />
            <stop offset="1" stop-color="white" />
        </linearGradient>
        <mask id="mask-l">
            <rect x="0" y="0" width="200" height="200" fill="url(#mask-lg-l)"  />
        </mask>

        <linearGradient id="mask-lg-r">
            <stop offset="0" stop-color="white" />
            <stop offset="1" stop-color="black" />
        </linearGradient>
        <mask id="mask-r">
            <rect x="0" y="0" width="200" height="200" fill="url(#mask-lg-r)"  />
        </mask>
        <circle cx="100" cy="100" r="100" fill="green" mask="url(#mask-l)"/>
        <rect x="0" y="0" width="200" height="200" fill="red" mask="url(#mask-r)" />
    </svg>
    `,
}, {
    id: 'animate',
    title: '利用animate移动图形',
    explain: `
    <p class=".body-1"><code>animate</code>标签可以让元素的一个属性运动起来</p>
    `,
    html: `
    <svg width="200" height="200" xmlns="http://www.w3.org/2000/svg">
        <rect x="0" y="0" width="100" height="100" fill="green">
            <animate
                attributeName="x" 
                from="0" 
                to="100"
                dur="2s" 
                repeatCount="indefinite"
            />
            <animate 
                attributeName="y" 
                from="0" 
                to="100"
                dur="2s" 
                repeatCount="indefinite"
            />
        </rect>
    </svg>
    `,
}, {
    id: 'use-animate',
    title: '利用animate绘制五角星',
    explain: `
    <p class=".body-1">1. <code>use</code>标签的作用是将页面上一个元素剪切到自己内部</p>
    <p class=".body-1">2. <code>defs</code>标签内部的元素不会渲染</p>
    `,
    html: `
    <svg width="200" height="200" xmlns="http://www.w3.org/2000/svg">
        <clipPath id="am-cp">
            <circle cx="50" cy="75" r="0">
                <animate
                    attributeName="r" 
                    from="0" 
                    to="60"
                    dur="5s" 
                    repeatCount="indefinite"
                />
            </circle>
        </clipPath>
        <defs>
            <polygon points="50,50 20,100 90,70 10,70 80,100" id="am-polygon"/>
        </defs>
        <use clip-path="url(#am-cp)" xlink:href="#am-polygon" fill="pink"/>
    </svg>
    `,
}, {
    id: 'animateMotion',
    title: '让一个图形沿着指定路径移动',
    explain: `
    <p class=".body-1"><code>animateMotion</code>标签可以传入提前定义的路径，让图形沿着路径移动</p>
    `,
    html: `
    <svg width="200" height="200" xmlns="http://www.w3.org/2000/svg">
        <path 
            d="M10 100 C 30,30 70,170 90,100 S 150,170 170,100" 
            fill="none" 
            stroke="black"
            id="am-path"
        />

        <circle cx="0" cy="0" r="5" fill="green">
            <animateMotion dur="6s" repeatCount="indefinite">
                <mpath xlink:href="#am-path"/>
            </animateMotion>
        </circle>
    </svg>
    `,
}, {
    id: 'animateTransform',
    title: '过渡动画',
    explain: `
    <p class=".body-1"><code>animateTransform</code>标签可以实现类似css的过渡动画，改变过渡的属性</p>
    `,
    html: `
    <svg width="200" height="200" xmlns="http://www.w3.org/2000/svg">
        <rect 
            x="50" 
            y="10" 
            width="30" 
            height="30"
            fill="green">

            <animateTransform 
                attributeName="transform"
                type="rotate"
                from="0 65 25"
                to="360 65 25"
                dur="3s"
                repeatCount="indefinite"
            />
        </rect>
    </svg>
    `,
}
            ],
        };
    }
};
</script>

