<template>
    <v-container fluid fill-height style="padding:0;" id="basis" grid-list-md>
        <Blog :items="items" :time="time" :title="title">
            <div class="blogcontent">
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="200"
                            >
                                <v-card-title>
                                    <h3 id="rect">绘制实心矩形</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <v-layout row wrap>
                                        <v-flex xs12 sm6 pr-1>
                                            <div class="part">
                                                <canvas id="canvas-rect" width="200px" height="200px"></canvas>
                                            </div>
                                        </v-flex>
                                        <v-flex xs12 sm6>
                                            <v-tabs color="light-green darken-1" dark slider-color="yellow">
                                                <v-tab key="explain" ripple>解释</v-tab>
                                                <v-tab key="html" ripple>HTML</v-tab>
                                                <v-tab key="js" ripple>JAVASCRIPT</v-tab>
                                                <v-tab-item key="explain">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <p>1. <code>fillRect(x, y, width, height)</code> 绘制实心矩形</p>
                                                            <p>2. <code>strokeRect(x, y, width, height)</code> 绘制空心矩形</p>
                                                            <p>3. <code>clearRect(x, y, width, height)</code> 清除一块矩形区域</p>
                                                            <p>4. <code>rect(x, y, width, height)</code> 绘制一个矩形路径，需要手动填充</p>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="html">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">
                                                                {{recthtml}}
                                                            </pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="js">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">{{rectjs}}</pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                            </v-tabs>
                                        </v-flex>
                                    </v-layout>
                                    
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                    <v-flex xs12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="200"
                            >
                                <v-card-title>
                                    <h3 id="path">使用路径可以绘制出任意多边形</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <v-layout row wrap>
                                        <v-flex xs12 sm6 pr-1>
                                            <div class="part">
                                                <canvas id="canvas-path" width="200px" height="200px"></canvas>
                                            </div>
                                        </v-flex>
                                        <v-flex xs12 sm6>
                                            <v-tabs color="light-green darken-1" dark slider-color="yellow">
                                                <v-tab key="explain" ripple>解释</v-tab>
                                                <v-tab key="html" ripple>HTML</v-tab>
                                                <v-tab key="js" ripple>JAVASCRIPT</v-tab>
                                                <v-tab-item key="explain">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <p>1. <code>beginPath()</code> 新建一条路径</p>
                                                            <p>2. <code>moveTo(x, y)</code> 将画笔移动到指定位置</p>
                                                            <p>3. <code>lineTo(x, y)</code> 绘制一条直线路径</p>
                                                            <p>4. <code>closePath()</code> 闭合路径</p>
                                                            <p>5. <code>fill()</code> 填充路径生成实心图形，不需要调用<code>closePath()</code></p>
                                                            <p>6. <code>stroke()</code> 描边，生成空心图形，需要调用<code>closePath()</code>闭合路径</p>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="html">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">
                                                                {{pathhtml}}
                                                            </pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="js">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">{{pathjs}}</pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                            </v-tabs>
                                        </v-flex>
                                    </v-layout>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                    <v-flex xs12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="200"
                            >
                                <v-card-title>
                                    <h3 id="arc">绘制圆和圆弧</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <v-layout row wrap>
                                        <v-flex xs12 sm6 pr-1>
                                            <div class="part">
                                                <canvas id="canvas-arc" width="200px" height="200px"></canvas>
                                            </div>
                                        </v-flex>
                                        <v-flex xs12 sm6>
                                            <v-tabs color="light-green darken-1" dark slider-color="yellow">
                                                <v-tab key="explain" ripple>解释</v-tab>
                                                <v-tab key="html" ripple>HTML</v-tab>
                                                <v-tab key="js" ripple>JAVASCRIPT</v-tab>
                                                <v-tab-item key="explain">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <p>1. 下面使用的方法全部都需要弧度，弧度和角度转换公式 <code>rad=(Math.PI/180)*ang</code></p>
                                                            <p>2. <code>arc(x, y, radius, startAngle, endAngle, anticlockwise)</code> 绘制一段圆弧，anticlockwise设置为true表示逆时针，默认是顺时针</p>
                                                            <p>3. <code>arcTo(x1, y1, x2, y2, radius)</code> 绘制两条线的切线圆弧，x1,y1表示第一条线的结束点第二条线的起始点，x2,y2表示第二条线的结束点，其中第一条线的起始点需要自己手动设置</p>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="html">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">
                                                                {{archtml}}
                                                            </pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="js">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">
                                                                {{arcjs}}
                                                            </pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                            </v-tabs>
                                        </v-flex>
                                    </v-layout>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                    <v-flex xs12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="200"
                            >
                                <v-card-title>
                                    <h3 id="bezier">贝塞尔曲线</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <v-layout row wrap>
                                        <v-flex xs12 sm6 pr-1>
                                            <div class="part">
                                                <canvas id="canvas-bezier" width="200px" height="200px"></canvas>
                                            </div>
                                        </v-flex>
                                        <v-flex xs12 sm6>
                                            <v-tabs color="light-green darken-1" dark slider-color="yellow">
                                                <v-tab key="explain" ripple>解释</v-tab>
                                                <v-tab key="html" ripple>HTML</v-tab>
                                                <v-tab key="js" ripple>JAVASCRIPT</v-tab>
                                                <v-tab-item key="explain">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <p>1. 贝塞尔曲线是绘制复杂优美线条的核心，关于贝塞尔曲线的知识请自行学习，在绘图的时候记住，曲线会向控制点的方向凹陷</p>
                                                            <p>2. <code>quadraticCurveTo(cp1x, cp1y, x, y)</code> 二次贝塞尔曲线，(cp1x,cp1y)表示控制点，(x,y)表示结束点，开始点要手动设置</p>
                                                            <p>3. <code>bezierCurveTo(cp1x, cp1y, cp2x, cp2y, x, y)</code> 三次贝塞尔曲线，原理同上</p>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="html">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">
                                                                {{bezierhtml}}
                                                            </pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="js">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">
                                                                {{bezierjs}}
                                                            </pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                            </v-tabs>
                                        </v-flex>
                                    </v-layout>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                    <v-flex xs12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="200"
                            >
                                <v-card-title>
                                    <h3 id="gradients">利用渐变制作天空中的太阳</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <v-layout row wrap>
                                        <v-flex xs12 sm6 pr-1>
                                            <div class="part">
                                                <canvas id="canvas-gradients" width="200px" height="200px"></canvas>
                                            </div>
                                        </v-flex>
                                        <v-flex xs12 sm6>
                                            <v-tabs color="light-green darken-1" dark slider-color="yellow">
                                                <v-tab key="explain" ripple>解释</v-tab>
                                                <v-tab key="html" ripple>HTML</v-tab>
                                                <v-tab key="js" ripple>JAVASCRIPT</v-tab>
                                                <v-tab-item key="explain">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <p>1. <code>createLinearGradient(x1, y1, x2, y2)</code> 创建线性渐变，参数表示渐变的起点 (x1,y1) 与终点 (x2,y2)</p>
                                                            <p>2. <code>createRadialGradient(x1, y1, r1, x2, y2, r2)</code> 创建径向渐变，前三个参数定义一个以 (x1,y1) 为原点，半径为 r1 的圆，后三个参数同理。</p>
                                                            <p>3. <code>addColorStop(position, color)</code> 创建渐变色，第一个参数表示渐变色的位置。</p>

                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="html">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">
                                                                {{gradientshtml}}
                                                            </pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="js">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">
                                                                {{gradientsjs}}
                                                            </pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                            </v-tabs>
                                        </v-flex>
                                    </v-layout>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                    <v-flex xs12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="200"
                            >
                                <v-card-title>
                                    <h3 id="patterns">利用模式实现图片平铺</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <v-layout row wrap>
                                        <v-flex xs12 sm6 pr-1>
                                            <div class="part">
                                                <canvas id="canvas-patterns" width="300px" height="200px"></canvas>
                                            </div>
                                        </v-flex>
                                        <v-flex xs12 sm6>
                                            <v-tabs color="light-green darken-1" dark slider-color="yellow">
                                                <v-tab key="explain" ripple>解释</v-tab>
                                                <v-tab key="html" ripple>HTML</v-tab>
                                                <v-tab key="js" ripple>JAVASCRIPT</v-tab>
                                                <v-tab-item key="explain">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <p><code>createPattern(image, type)</code> 创建模式，type表示重复的方式，和css的background-repeat参数一样</p>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="html">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">
                                                                {{patternshtml}}
                                                            </pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="js">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">
                                                                {{patternsjs}}
                                                            </pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                            </v-tabs>
                                        </v-flex>
                                    </v-layout>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                    <v-flex xs12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="200"
                            >
                                <v-card-title>
                                    <h3 id="shadow">创建阴影</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <v-layout row wrap>
                                        <v-flex xs12 sm6 pr-1>
                                            <div class="part">
                                                <canvas id="canvas-shadow" width="200px" height="200px"></canvas>
                                            </div>
                                        </v-flex>
                                        <v-flex xs12 sm6>
                                            <v-tabs color="light-green darken-1" dark slider-color="yellow">
                                                <v-tab key="explain" ripple>解释</v-tab>
                                                <v-tab key="html" ripple>HTML</v-tab>
                                                <v-tab key="js" ripple>JAVASCRIPT</v-tab>
                                                <v-tab-item key="explain">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <p>1. <code>shadowOffsetX</code> 设置阴影横向便宜</p>
                                                            <p>2. <code>shadowOffsetY</code> 设置阴影纵向便宜</p>
                                                            <p>3. <code>shadowBlur</code> 设置阴影模糊程度</p>
                                                            <p>4. <code>shadowColor</code> 设置阴影颜色</p>
                                                            <p>5. <code>fill("evenodd")</code> 设置重叠图形间隔显示</p>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="html">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">
                                                                {{shadowhtml}}
                                                            </pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="js">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">
                                                                {{shadowjs}}
                                                            </pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                            </v-tabs>
                                        </v-flex>
                                    </v-layout>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                    <v-flex xs12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="200"
                            >
                                <v-card-title>
                                    <h3 id="text">绘制文本</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <v-layout row wrap>
                                        <v-flex xs12 sm6 pr-1>
                                            <div class="part">
                                                <canvas id="canvas-text" width="200px" height="200px"></canvas>
                                            </div>
                                        </v-flex>
                                        <v-flex xs12 sm6>
                                            <v-tabs color="light-green darken-1" dark slider-color="yellow">
                                                <v-tab key="explain" ripple>解释</v-tab>
                                                <v-tab key="html" ripple>HTML</v-tab>
                                                <v-tab key="js" ripple>JAVASCRIPT</v-tab>
                                                <v-tab-item key="explain">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <p>1. <code>fillText(text, x, y)</code> 绘制实心文本</p>
                                                            <p>2. <code>strokeText(text, x, y)</code> 绘制空心文本</p>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="html">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">
                                                                {{texthtml}}
                                                            </pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="js">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">
                                                                {{textjs}}
                                                            </pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                            </v-tabs>
                                        </v-flex>
                                    </v-layout>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                    <v-flex xs12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="200"
                            >
                                <v-card-title>
                                    <h3 id="img">图片的裁剪和缩放</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <v-layout row wrap>
                                        <v-flex xs12 sm6 pr-1>
                                            <div class="part">
                                                <canvas id="canvas-img" width="400px" height="300px"></canvas>
                                            </div>
                                        </v-flex>
                                        <v-flex xs12 sm6>
                                            <v-tabs color="light-green darken-1" dark slider-color="yellow">
                                                <v-tab key="explain" ripple>解释</v-tab>
                                                <v-tab key="html" ripple>HTML</v-tab>
                                                <v-tab key="js" ripple>JAVASCRIPT</v-tab>
                                                <v-tab-item key="explain">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <p>1. <code>drawImage(image, x, y)</code> 按照图片的原始大小绘制在 x,y 处</p>
                                                            <p>2. <code>drawImage(image, x, y, width, height)</code> 按照指定的宽度和高度缩放图片</p>
                                                            <p>3. <code>drawImage(image, sx, sy, sWidth, sHeight, dx, dy, dWidth, dHeight)</code> 带s的参数表示从原始图片指定位置裁剪出指定大小的图，带d的参数表示将裁剪出来的图像渲染到指定大小的区域中</p>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="html">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">
                                                                {{imghtml}}
                                                            </pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="js">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">
                                                                {{imgjs}}
                                                            </pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                            </v-tabs>
                                        </v-flex>
                                    </v-layout>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                </v-layout>
            </div>
        </Blog>
    </v-container>
</template>

<script>
import Blog from "../Blog.vue";
import roadurl from "../../assets/road.png";
export default {
    components: { Blog },
    data() {
        return {
            canvasGradients: null,
            time: "2018-9-20",
            title: "canvas基础应用",
            items: [
                {
                    title: "绘制实心矩形",
                    target: "#rect"
                },
                { divider: true },
                {
                    title: "使用路径可以绘制出任意多边形",
                    target: "#path"
                },
                { divider: true },
                {
                    title: "绘制圆和圆弧",
                    target: "#arc"
                },
                { divider: true },
                {
                    title: "贝塞尔曲线",
                    target: "#bezier"
                },
                { divider: true },
                {
                    title: "利用渐变制作天空中的太阳",
                    target: "#gradients"
                },
                { divider: true },
                {
                    title: "利用模式实现图片平铺",
                    target: "#patterns"
                },
                { divider: true },
                {
                    title: "创建阴影",
                    target: "#shadow"
                },
                { divider: true },
                {
                    title: "绘制文本",
                    target: "#text"
                },
                { divider: true },
                {
                    title: "图片的裁剪和缩放",
                    target: "#img"
                }
            ],
            recthtml: `
            <canvas 
                id="canvas-rect" 
                width="200px" 
                height="200px"
            >
            </canvas>
            `,
            rectjs: `
            const canvasRect = document.getElementById('canvas-rect');
            const ctxR = canvasRect.getContext('2d');
            ctxR.fillStyle = 'green';
            ctxR.strokeStyle = 'darkseagreen';
            // 方式一
            ctxR.fillRect(10, 10, 80, 50);
            // 方式二
            ctxR.rect(10, 70, 80, 50);
            ctxR.fill();
            // 方式三
            ctxR.strokeRect(10, 130, 80, 50);
            // 方式四
            ctxR.fillRect(120, 10, 80, 110);
            ctxR.clearRect(140, 30, 40, 70);
            `,
            pathhtml: `
            <canvas 
                id="canvas-path" 
                width="200px" 
                height="200px"
            >
            </canvas>
            `,
            pathjs: `
            const canvasPath = document.getElementById('canvas-path');
            const ctxP = canvasPath.getContext('2d');
            ctxP.fillStyle = 'green';
            ctxP.strokeStyle = 'darkseagreen';
            // 实心五边形
            ctxP.beginPath();
            ctxP.moveTo(30, 10);
            ctxP.lineTo(80, 10);
            ctxP.lineTo(90, 30);
            ctxP.lineTo(80, 50);
            ctxP.lineTo(30, 50);
            ctxP.lineTo(20, 30);
            ctxP.fill();
            // 空心五边形
            ctxP.beginPath();
            ctxP.moveTo(30, 70);
            ctxP.lineTo(80, 70);
            ctxP.lineTo(90, 90);
            ctxP.lineTo(80, 110);
            ctxP.lineTo(30, 110);
            ctxP.lineTo(20, 90);
            ctxP.closePath();
            ctxP.stroke();
            `,
            archtml: `
            <canvas 
                id="canvas-arc" 
                width="200px" 
                height="200px"
            >
            </canvas>
            `,
            arcjs: `
            const canvasArc = document.getElementById('canvas-arc');
            const ctxA = canvasArc.getContext('2d');
            ctxA.fillStyle = 'green';
            ctxA.strokeStyle = 'darkseagreen';

            // 绘制圆
            ctxA.beginPath();
            ctxA.arc(50, 50, 40, 0, (Math.PI/180)*360, false);
            ctxA.fill();

            // 绘制圆切片
            ctxA.beginPath();
            ctxA.arc(50, 140, 40, 0, (Math.PI/180)*120, true);
            ctxA.fill();

            // 绘制圆角
            ctxA.beginPath();
            ctxA.moveTo(190, 10);
            ctxA.arcTo(110, 10, 110, 70, 30);
            ctxA.lineTo(110, 70);
            ctxA.stroke();
            `,
            bezierhtml: `
            <canvas 
                id="canvas-bezier" 
                width="200px" 
                height="200px"
            >
            </canvas>
            `,
            bezierjs: `
            const canvasBezier= document.getElementById('canvas-bezier');
            const ctxB = canvasBezier.getContext('2d');
            ctxB.fillStyle = 'green';
            ctxB.strokeStyle = 'darkseagreen';

            // 二次贝塞尔曲线
            ctxB.beginPath();
            ctxB.moveTo(30, 10);
            ctxB.quadraticCurveTo(50, 20, 30, 30);
            ctxB.quadraticCurveTo(10, 20, 30, 10);
            ctxB.moveTo(30, 30);
            ctxB.quadraticCurveTo(70, 50, 30, 70);
            ctxB.quadraticCurveTo(-10, 50, 30, 30);
            ctxB.stroke();

            // 三次贝塞尔曲线
            ctxB.beginPath();
            ctxB.moveTo(10, 100);
            ctxB.bezierCurveTo(30, 80, 70, 120, 100, 100);
            ctxB.stroke();
            `,
            gradientshtml: `
            <canvas 
                id="canvas-gradients" 
                width="200px" 
                height="200px"
            >
            </canvas>
            `,
            gradientsjs: `
            const canvasGradients= document.getElementById('canvas-gradients');
            const ctxG = canvasGradients.getContext('2d');
            // 天空和草地
            const sky = ctxG.createLinearGradient(10, 10, 10, 210);
            sky.addColorStop(0, 'skyblue');
            sky.addColorStop(0.4, 'skyblue');
            sky.addColorStop(0.6, '#c8e3ef');
            sky.addColorStop(0.7, '#f6f7f7');
            sky.addColorStop(0.8, '#bff1b3');
            sky.addColorStop(1, '#26C000');
            ctxG.fillStyle = sky;
            ctxG.rect(10, 10, 200, 200);
            ctxG.fill();

            // 太阳
            ctxG.beginPath();
            const sun = ctxG.createRadialGradient(100, 50, 5, 100, 50, 20);
            sun.addColorStop(0, '#fff');
            sun.addColorStop(1, 'skyblue');
            ctxG.arc(100, 50, 20, 0, (Math.PI/180)*360, false);
            ctxG.fillStyle = sun;
            ctxG.fill();
            `,
            patternshtml: `
            <canvas 
                id="canvas-patterns" 
                width="300px" 
                height="200px"
            >
            </canvas>
            `,
            patternsjs: `
            const canvasPatterns= document.getElementById('canvas-patterns');
            const ctxP = canvasPatterns.getContext('2d');
            const img = new Image();
            img.src = roadurl;
            img.onload = function() {
                ctxP.fillStyle = ctxP.createPattern(img, 'repeat');
                ctxP.fillRect(10, 10, 300, 200);
            }
            `,
            shadowhtml: `
            <canvas 
                id="canvas-shadow" 
                width="300px" 
                height="200px"
            >
            </canvas>
            `,
            shadowjs: `
            const canvasShadow= document.getElementById('canvas-shadow');
            const ctxS = canvasShadow.getContext('2d');
            ctxS.fillStyle = 'green';

            ctxS.shadowOffsetX = 3;
            ctxS.shadowOffsetY = 3;
            ctxS.shadowBlur = 5;
            ctxS.shadowColor = "rgba(0, 0, 0, 0.5)";
            ctxS.fillStyle = 'green';
            ctxS.beginPath(); 
            ctxS.arc(50, 50, 30, 0, Math.PI*2, true);
            ctxS.arc(50, 50, 20, 0, Math.PI*2, true);
            ctxS.arc(50, 50, 10, 0, Math.PI*2, true);
            ctxS.fill("evenodd");
            `,
            texthtml: `
            <canvas 
                id="canvas-text" 
                width="200px" 
                height="200px"
            >
            </canvas>
            `,
            textjs: `
            const canvasText = document.getElementById('canvas-text');
            const ctxT = canvasText.getContext('2d');
            ctxT.fillStyle = 'green';
            ctxT.strokeStyle = 'darkseagreen';
            ctxT.font = "25px cursive";
            ctxT.fillStyle = 'black';
            ctxT.fillText("Canvas", 10, 30);
            ctxT.strokeText("Canvas", 10, 60);
            `,
            imghtml: `
            <canvas 
                id="canvas-img" 
                width="400px" 
                height="300px"
            >
            </canvas>
            `,
            imgjs: `
            const canvasImg = document.getElementById('canvas-img');
            const ctxI = canvasImg.getContext('2d');

            // 原始大小绘制图片
            ctxI.drawImage(canvasGradients, 10, 10);
            // 指定图片的大小，按照指定的大小缩放图片
            ctxI.drawImage(canvasGradients, 220, 10, 100, 100);
            // 裁剪部分图片
            ctxI.drawImage(canvasGradients, 75, 25, 50, 50, 225, 120, 50, 50);
            `,
        };
    },
    mounted() {
        this.drawRect();
        this.drawPath();
        this.drawArc();
        this.drawBezier();
        this.drawGradients();
        this.drawPatterns();
        this.drawShadow();
        this.drawText();
        this.drawImg(this.canvasGradients);
    },
    methods: {
        drawRect() {
            const canvasRect = document.getElementById('canvas-rect');
            const ctxR = canvasRect.getContext('2d');
            ctxR.fillStyle = 'green';
            ctxR.strokeStyle = 'darkseagreen';
            // 方式一
            ctxR.fillRect(10, 10, 80, 50);
            // 方式二
            ctxR.rect(10, 70, 80, 50);
            ctxR.fill();
            // 方式三
            ctxR.strokeRect(10, 130, 80, 50);
            // 方式四
            ctxR.fillRect(120, 10, 80, 110);
            ctxR.clearRect(140, 30, 40, 70);
        },
        drawPath() {
            const canvasPath = document.getElementById('canvas-path');
            const ctxP = canvasPath.getContext('2d');
            ctxP.fillStyle = 'green';
            ctxP.strokeStyle = 'darkseagreen';
            // 实心五边形
            ctxP.beginPath();
            ctxP.moveTo(30, 10);
            ctxP.lineTo(80, 10);
            ctxP.lineTo(90, 30);
            ctxP.lineTo(80, 50);
            ctxP.lineTo(30, 50);
            ctxP.lineTo(20, 30);
            ctxP.fill();
            // 空心五边形
            ctxP.beginPath();
            ctxP.moveTo(30, 70);
            ctxP.lineTo(80, 70);
            ctxP.lineTo(90, 90);
            ctxP.lineTo(80, 110);
            ctxP.lineTo(30, 110);
            ctxP.lineTo(20, 90);
            ctxP.closePath();
            ctxP.stroke();
        },
        drawArc() {
            const canvasArc = document.getElementById('canvas-arc');
            const ctxA = canvasArc.getContext('2d');
            ctxA.fillStyle = 'green';
            ctxA.strokeStyle = 'darkseagreen';

            // 绘制圆
            ctxA.beginPath();
            ctxA.arc(50, 50, 40, 0, (Math.PI/180)*360, false);
            ctxA.fill();

            // 绘制圆切片
            ctxA.beginPath();
            ctxA.arc(50, 140, 40, 0, (Math.PI/180)*120, true);
            ctxA.fill();

            // 绘制圆角
            ctxA.beginPath();
            ctxA.moveTo(190, 10);
            ctxA.arcTo(110, 10, 110, 70, 30);
            ctxA.lineTo(110, 70);
            ctxA.stroke();
        },
        drawBezier() {
            const canvasBezier= document.getElementById('canvas-bezier');
            const ctxB = canvasBezier.getContext('2d');
            ctxB.fillStyle = 'green';
            ctxB.strokeStyle = 'darkseagreen';

            // 二次贝塞尔曲线
            ctxB.beginPath();
            ctxB.moveTo(30, 10);
            ctxB.quadraticCurveTo(50, 20, 30, 30);
            ctxB.quadraticCurveTo(10, 20, 30, 10);
            ctxB.moveTo(30, 30);
            ctxB.quadraticCurveTo(70, 50, 30, 70);
            ctxB.quadraticCurveTo(-10, 50, 30, 30);
            ctxB.stroke();

            // 三次贝塞尔曲线
            ctxB.beginPath();
            ctxB.moveTo(10, 100);
            ctxB.bezierCurveTo(30, 80, 70, 120, 100, 100);
            ctxB.stroke();
        },
        drawGradients() {
            const canvasGradients= document.getElementById('canvas-gradients');
            this.canvasGradients = canvasGradients;
            const ctxG = canvasGradients.getContext('2d');
            // 天空和草地
            const sky = ctxG.createLinearGradient(10, 10, 10, 210);
            sky.addColorStop(0, 'skyblue');
            sky.addColorStop(0.4, 'skyblue');
            sky.addColorStop(0.6, '#c8e3ef');
            sky.addColorStop(0.7, '#f6f7f7');
            sky.addColorStop(0.8, '#bff1b3');
            sky.addColorStop(1, '#26C000');
            ctxG.fillStyle = sky;
            ctxG.rect(10, 10, 200, 200);
            ctxG.fill();

            // 太阳
            ctxG.beginPath();
            const sun = ctxG.createRadialGradient(100, 50, 5, 100, 50, 20);
            sun.addColorStop(0, '#fff');
            sun.addColorStop(1, 'skyblue');
            ctxG.arc(100, 50, 20, 0, (Math.PI/180)*360, false);
            ctxG.fillStyle = sun;
            ctxG.fill();
        },
        drawPatterns() {
            const canvasPatterns= document.getElementById('canvas-patterns');
            const ctxP = canvasPatterns.getContext('2d');
            const img = new Image();
            img.src = roadurl;
            img.onload = function() {
                ctxP.fillStyle = ctxP.createPattern(img, 'repeat');
                ctxP.fillRect(10, 10, 300, 200);
            }
        },
        drawShadow() {
            const canvasShadow= document.getElementById('canvas-shadow');
            const ctxS = canvasShadow.getContext('2d');
            ctxS.fillStyle = 'green';

            ctxS.shadowOffsetX = 3;
            ctxS.shadowOffsetY = 3;
            ctxS.shadowBlur = 5;
            ctxS.shadowColor = "rgba(0, 0, 0, 0.5)";
            ctxS.fillStyle = 'green';
            ctxS.beginPath(); 
            ctxS.arc(50, 50, 30, 0, Math.PI*2, true);
            ctxS.arc(50, 50, 20, 0, Math.PI*2, true);
            ctxS.arc(50, 50, 10, 0, Math.PI*2, true);
            ctxS.fill("evenodd");
        },
        drawText() {
            const canvasText = document.getElementById('canvas-text');
            const ctxT = canvasText.getContext('2d');
            ctxT.fillStyle = 'green';
            ctxT.strokeStyle = 'darkseagreen';
            ctxT.font = "25px cursive";
            ctxT.fillStyle = 'black';
            ctxT.fillText("Canvas", 10, 30);
            ctxT.strokeText("Canvas", 10, 60);
        },
        drawImg(canvasGradients) {
            const canvasImg = document.getElementById('canvas-img');
            const ctxI = canvasImg.getContext('2d');

            // 原始大小绘制图片
            ctxI.drawImage(canvasGradients, 10, 10);
            // 指定图片的大小，按照指定的大小缩放图片
            ctxI.drawImage(canvasGradients, 220, 10, 100, 100);
            // 裁剪部分图片
            ctxI.drawImage(canvasGradients, 75, 25, 50, 50, 225, 120, 50, 50);
        }
    }
};
</script>

